<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="mx-auto">
        <v-card-title class="justify-center">Formulación contractual preliminar</v-card-title>
        <v-card-text>
            <v-row>
                <v-col cols="12">
                    <v-text-field
                        v-model="search"
                        append-icon="search"
                        label="Buscar"
                        single-line
                        hide-details
                    >
                    </v-text-field>
                    <br>
                    <v-data-table
                        :headers="headers"
                        :items="dataConsulta"
                        :search="search"
                        class="elevation-1"
                    >
                      <template v-slot:item.verOferentes="{ item }">
                        <v-icon
                          class="mr-5"
                          color="primary"
                          @click="oferentesPropuestas(item.id,item.titulo)"
                        >
                          person
                        </v-icon>
                      </template>
                    </v-data-table>
                </v-col>
            </v-row>
        </v-card-text>
      </v-card>
    </v-flex>

    <v-dialog
      v-model="dialog_oferentes"
      persistent
      max-width="1200"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center">
          {{ `Adjudicación para el proceso '${tituloOferentes}'` }}
        </v-card-title>
        <v-card-text>
          <v-text-field
              v-model="searchOferente"
              append-icon="search"
              label="Buscar"
              single-line
              hide-details
          >
          </v-text-field>
          <br>
          <v-data-table
              :headers="headersOferente"
              :items="oferentes"
              :search="searchOferente"
              class="elevation-1"
              :single-select="true"
              show-select
              v-model="selProponente"
              item-key="proponente"
          >
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="adjudicarProceso" :disabled="selProponente.length <= 0">Adjudicar</v-btn>
          <v-btn color="primary" dark text @click="dialog_oferentes=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { EventBus } from '@/main'
import { adminApp, anunciosProcAdquisicion } from '@/services/api'
export default {
  name: 'formulacionContractualPre',
  data () {
    return {
      search: '',
      tipo_evaluacion: '',
      oferentes: [],
      dialog_oferentes: false,
      searchOferente: '',
      tituloOferentes: '',
      selProponente: [],
      arrTipoEvaluacion: [
        { value: 'PM', text: 'Proceso Manual' },
        { value: 'AC', text: 'Algoritmo de cálculo' }
      ],
      dataConsulta: [],
      headers: [
        { text: 'Título del proceso', align: 'left', value: 'titulo', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha límite', align: 'left', value: 'fecha_cierre_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Tipo de proceso', align: 'left', value: 'tipo_contratacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Estado', align: 'left', value: 'estado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Referencia', align: 'left', value: 'referencia', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Oferentes', align: 'center', value: 'verOferentes', class: 'subtitle-1 font-weight-bold black--text' }
      ],
      headersOferente: [
        { text: 'Nombre/Razón social', align: 'left', value: 'Nombre', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Identificación', align: 'left', value: 'identificacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha', align: 'left', value: 'fecha', class: 'subtitle-1 font-weight-bold black--text' }
      ]
    }
  },
  methods: {
    async propuestasCerradas () {
      try {
        const { data } = await adminApp.propuestasCerradas()
        this.dataConsulta = data
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async oferentesPropuestas (codigopropuesta, nombre) {
      try {
        const { data } = await adminApp.oferentesPropuestas({ codigopropuesta: codigopropuesta })
        this.oferentes = data
        this.tituloOferentes = nombre
        this.dialog_oferentes = true
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async adjudicarProceso () {
      if (this.selProponente.length <= 0) {
        this.$toasted.error('No se ha seleccionado ningún oferente', { icon: 'error', duration: 5000 })
        return
      }

      const codigopropuesta = this.selProponente[0].consecutivo
      const contratista = this.selProponente[0].proponente
      const llave = this.selProponente[0].llave
      const titulopropuesta = this.selProponente[0].titulopropuesta

      try {
        const { data } = await adminApp.adjudicarProceso(
          {
            proceso: codigopropuesta,
            contratista: contratista
          }
        )
        if (data.result) {
          if (data.result === 'ok') {
            this.$toasted.success(data.mensaje, { icon: 'done', duration: 5000 })
            this.guardarMensaje(llave, titulopropuesta)
            this.propuestasCerradas()
            EventBus.$emit('actualizarPropuesta')
            this.dialog_oferentes = false
          } else {
            this.$toasted.error(data.mensaje, { icon: 'error', duration: 5000 })
          }
        } else {
          this.$toasted.error('Se presentó un error al intentar de realizar él proceso', { icon: 'error', duration: 5000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async guardarMensaje (Idpcr, NomProceso) {
      const mensaje = "Felicitaciones!!. Se le ha adjudicado el proceso '" + NomProceso + "'"
      const idpadre = 0
      const { data } = await anunciosProcAdquisicion.agregarMensaje({
        codigoconvreg: Idpcr,
        mensaje: mensaje,
        padre: idpadre,
        estado: 'E',
        userid: this.$session.get('usuarioid'),
        necesitacodpadre: '1'
      })
      if (data.respuesta) {
        console.log(data.respuesta)
      } else {
        // this.$toasted.error('Se presentó error al intentar de agregar el Mensaje', { icon: 'error', duration: 5000 })
      }
    }
  },
  mounted () {
    this.propuestasCerradas()
  }
}
</script>
